<template>
  <div class="home">
    <node-tree v-if="$yin.me._id"/>
  </div>
</template>

<script lang="ts">
import {defineComponent} from 'vue';
import NodeTree from "@/components/node/nodeTree.vue";

export default defineComponent({
  name: 'HomeView',
  components: {NodeTree},
  data() {
    return {
      msg: null,
      me: {}
    }
  },
  methods: {},
  mounted() {
    if (!this.$yin.me._id)
      this.$router.push('/auth')
    else {
      this.me = this.$yin.me
    }
  },
  watch: {
    me(val) {
      if (!val._id)
        this.$router.push('/auth')
    }
  }
});
</script>

